.calculator {
  font-size: 14px;
  height: 415px;
  color: #5E7A7D;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.formInput {
  position: relative;
}

.formInput input {
  font-family: 'Space Mono', monospace;
  background-color: #f3f8fb;
  border: 3px solid #f3f8fb;
  border-radius: 5px;
  width: calc(100% - 8px);
  font-size: 24px;
  outline: none;
  transition: all 250ms ease-in-out;
  color: #00494D;
  font-weight: bold;
  text-align: right;
}
.formInput input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.formInput input[type=number] {
  -moz-appearance: textfield;
}

.formInput input::placeholder {
  color: #a5b7b7;
  font-weight: normal;
}

.formInput input:focus {
  border: 3px solid #26C0AB;
  outline: none;
}

.formInput span {
  width: 40px;
  height: 29px;
  text-align: center;
  padding-top: 10px;
  position: absolute;
}

.percents {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(auto-fit, minmax(115px, 1fr));
}

.percents > button {
  background-color: #00494D;
  border: 5px solid #00494D;
  color: #FFF;
  font-family: 'Space Mono', monospace;
  font-size: 24px;
  height: 46px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.695, 0.135, 0.935, 0.145);
}

.percents > button.active {
  background-color: #26C0AB;
  color: #00494D;
  border: 2px solid #26C0AB;
  font-weight: bold;
}

.percents > button:hover {
  color: #00494D;
  font-weight: bold;
  background-color: #9fe8e9;
  border: 2px solid #9fe8e9;
}

.percents > input {
  background-color: #f3f8fb;
  border: 5px solid #f3f8fb;
  border-radius: 5px;
  width: calc(100% - 8px);
  font-size: 24px;
  outline: none;
  font-family: 'Space Mono', monospace;
}

.percents > input::placeholder {
  padding-left: 7px;
  color: #5E7A7D;
  font-family: 'Space Mono', monospace;
  font-weight: bold;
}
